            <el-descriptions-item label="ç‰ˆæœ¬">{{ uuidVersion.toUpperCase() }}</el-descriptions-item>
            <el-descriptions-item label="æ ¼å¼">{{ getFormatDescription() }}</el-descriptions-item>
            <el-descriptions-item label="é•¿åº¦">{{ generatedUUIDs[0] ? generatedUUIDs[0].length : 0 }} å­—ç¬¦</el-descriptions-item>
