<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'UuidGeneratorView',
  metaInfo: {
    title: 'UUIDç”Ÿæˆå™¨ - é€šç”¨å”¯ä¸€è¯†åˆ«ç ç”Ÿæˆå·¥å…·',
    meta: [
      { name: 'description', content: 'åœ¨çº¿UUIDç”Ÿæˆå™¨ï¼Œæ”¯æŒUUID v1ã€v4ã€v5ç‰ˆæœ¬ç”Ÿæˆï¼Œæä¾›æ‰¹é‡ç”Ÿæˆå’Œå¤šç§æ ¼å¼é€‰é¡¹ã€‚' },
      { name: 'keywords', content: 'UUIDç”Ÿæˆå™¨,GUID,å”¯ä¸€æ ‡è¯†ç¬¦,éšæœºID,åœ¨çº¿å·¥å…·' }
    ]
  },
  data() {
    return {
      uuidVersion: 'v4',
      generateCount: 10,
      formatOptions: [],
      namespace: '6ba7b810-9dad-11d1-80b4-00c04fd430c8',
      nameInput: '',
      generatedUUIDs: []
    }
  },
  mounted() {
    this.generateUUIDs()
  },
  methods: {
    generateUUIDs() {
      this.generatedUUIDs = []
      
      for (let i = 0; i < this.generateCount; i++) {
        let uuid = this.generateSingleUUID()
        uuid = this.formatUUID(uuid)
        this.generatedUUIDs.push(uuid)
      }
    },

    generateSingleUUID() {
      switch (this.uuidVersion) {
        case 'v1':
          return this.generateUUIDv1()
        case 'v4':
          return this.generateUUIDv4()
        case 'v5':
          return this.generateUUIDv5()
        default:
          return this.generateUUIDv4()
      }
    },

    generateUUIDv1() {
      // ç®€åŒ–çš„UUID v1å®ç°
      const timestamp = Date.now()
      const random = Math.random().toString(16).substring(2, 15)
      
      return [
        (timestamp & 0xffffffff).toString(16).padStart(8, '0'),
        ((timestamp >> 32) & 0xffff).toString(16).padStart(4, '0'),
        (0x1000 | ((timestamp >> 48) & 0x0fff)).toString(16),
        (0x8000 | (Math.random() * 0x3fff)).toString(16),
        random.padStart(12, '0')
      ].join('-')
    },

    generateUUIDv4() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = Math.random() * 16 | 0
        const v = c === 'x' ? r : (r & 0x3 | 0x8)
        return v.toString(16)
      })
    },

    generateUUIDv5() {
      if (!this.nameInput) {
        return this.generateUUIDv4() // å¦‚æœæ²¡æœ‰åç§°ï¼Œå›é€€åˆ°v4
      }
      
      // ç®€åŒ–çš„UUID v5å®ç°ï¼ˆå®é™…åº”è¯¥ä½¿ç”¨SHA-1ï¼‰
      const input = this.namespace + this.nameInput
      let hash = 0
      for (let i = 0; i < input.length; i++) {
        const char = input.charCodeAt(i)
        hash = ((hash << 5) - hash) + char
        hash = hash & hash // è½¬æ¢ä¸º32ä½æ•´æ•°
      }
      
      const hex = Math.abs(hash).toString(16).padStart(8, '0')
      return [
        hex.substring(0, 8),
        hex.substring(0, 4),
        '5' + hex.substring(1, 4),
        (0x8000 | (Math.random() * 0x3fff)).toString(16),
        hex.repeat(3).substring(0, 12)
      ].join('-')
    },

    formatUUID(uuid) {
      let formatted = uuid

      if (this.formatOptions.includes('uppercase')) {
        formatted = formatted.toUpperCase()
      }

      if (this.formatOptions.includes('nohyphens')) {
        formatted = formatted.replace(/-/g, '')
      }

      if (this.formatOptions.includes('braces')) {
        formatted = `{${formatted}}`
      }

      return formatted
    },

    getFormatDescription() {
      const formats = []
      if (this.formatOptions.includes('uppercase')) formats.push('å¤§å†™')
      if (this.formatOptions.includes('nohyphens')) formats.push('æ— è¿å­—ç¬¦')
      if (this.formatOptions.includes('braces')) formats.push('èŠ±æ‹¬å·')
      return formats.length > 0 ? formats.join(', ') : 'æ ‡å‡†æ ¼å¼'
    },

    getUniquenessDescription() {
      switch (this.uuidVersion) {
        case 'v1':
          return 'åŸºäºæ—¶é—´æˆ³ï¼ŒåŒä¸€æ¯«ç§’å†…å¯èƒ½é‡å¤'
        case 'v4':
          return 'å®Œå…¨éšæœºï¼Œé‡å¤æ¦‚ç‡æä½'
        case 'v5':
          return 'ç¡®å®šæ€§ï¼Œç›¸åŒè¾“å…¥äº§ç”Ÿç›¸åŒç»“æœ'
        default:
          return 'æœªçŸ¥'
      }
    },

    async copyUUID(uuid) {
      try {
        const success = await ClipboardUtil.copyText(uuid)
        if (success) {
          this.$message.success('UUIDå·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },

    async copyAllUUIDs() {
      try {
        const allUUIDs = this.generatedUUIDs.join('\n')
        const success = await ClipboardUtil.copyText(allUUIDs)
        if (success) {
          this.$message.success('æ‰€æœ‰UUIDå·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },

    downloadUUIDs() {
      if (this.generatedUUIDs.length === 0) {
        this.$message.warning('æ²¡æœ‰å¯ä¸‹è½½çš„UUID')
        return
      }
      const content = this.generatedUUIDs.join('\n')
      const filename = `uuids_${this.uuidVersion}_${Date.now()}.txt`;
      DownloadUtil.downloadText(content,filename);
      this.$message.success('UUIDæ–‡ä»¶ä¸‹è½½æˆåŠŸ')
    }
  }
}
</script>

